@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  --black: #000000;
  --white: #ffff;

  /* Main Colors */
  --blue: #5bd4ec;
  --main: #694bc7;
  --subtext: #565969;

  /* Background Colors */
  --greybg: #0f1013;
  --darkbg: #0b0c0e;
}


.themegreybg {
  background-color: #0f1013;
  background-color: var(--greybg);
}

.themeblackbg {
  background-color: #0b0c0e;
  background-color: var(--darkbg);
}
.col, .row {
  padding: 0!important;
  margin: 0!important;
}


.custom-container{
  height: 100vh;
  overflow: auto;
  padding-bottom: 40px;
}
.title {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 25px;
  color: white;
}
.form-label{
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: rgb(103 105 115);
}

/* Text Styles */
.md16White {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  color: white;
  font-size: 16px;
}
.md40white{
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 40px;
  color: white;
}
.rg20grey{
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
